import { useState, useEffect } from "react";
import { Text } from "../../../styledCpns/DataDisplay";
import { Box } from "../../../styledCpns/Layout";
import { Select } from "../../../styledCpns/Inputs";
import { CloseBtn } from "../../CommonCpns";
import { TalentInfo, SkillAttributes } from "./DetailsContent";
import { SlideShow } from "../styles";

export default function Details(props) {
  const { index, setIndex } = props;
  const [lv, setLv] = useState(1);
  const [tab, setTab] = useState("Talent Info");
  const { code, actvTalents } = props.charData;
  const isActv = index < actvTalents.length;

  useEffect(() => {
    if (!isActv && tab === "Skill Attributes") {
      setTab("Talent Info");
    }
  }, [isActv, tab]);

  useEffect(() => {
    setTab("Talent Info");
    setLv(1);
  }, [code]);

  if (index === -1) {
    return <div className="details close" />;
  }
  const { beta, icon, vision, pasvTalents } = props.charData;
  const needSelect = isActv && actvTalents[index].type !== "Alternate Sprint";
  let { name, desc } = isActv
    ? actvTalents[index]
    : pasvTalents[index - actvTalents.length];

  return (
    <div className={props.className}>
      <div className="full-h flex-col">
        <div className="grow-1 hide-sb">
          <SlideShow
            forTl
            index={index}
            images={[...actvTalents, ...pasvTalents].map((tl) =>
              beta ? icon : tl.image
            )}
            back={() => setIndex(index - 1)}
            next={() => setIndex(index + 1)}
          />
          <Text variant="h5" color={vision} fontWeight={700} align="center">
            {name}
          </Text>
          <div className={(needSelect ? "pl-3" : "pr-3") + " flex-center"}>
            <Text className="mr-1" variant="h6">
              Lv.
            </Text>
            {needSelect ? (
              <Select
                className="pr-2"
                fontSize="1.125rem"
                fontWeight={700}
                color="white"
                value={lv}
                onChange={(e) => setLv(+e.target.value)}
              >
                {[...Array(15).keys()].map((_, i) => (
                  <option key={i}>{i + 1}</option>
                ))}
              </Select>
            ) : (
              <Text className="px-1" variant="h6" fontWeight={700}>
                1
              </Text>
            )}
          </div>
          <div className="mt-2 flex">
            {["Talent Info", "Skill Attributes"].map((text, i) => (
              <Tab
                key={i}
                text={text}
                tab={tab}
                setTab={setTab}
                isActv={isActv}
              />
            ))}
          </div>
          {tab === "Talent Info" ? (
            <TalentInfo desc={desc} isActv={isActv} />
          ) : (
            <SkillAttributes tlData={actvTalents[index]} lv={lv} />
          )}
        </div>
        <div className="mt-3">
          <CloseBtn className="mx-auto glow-on-hover" onClick={props.close} />
        </div>
      </div>
      <div></div>
    </div>
  );
}

const Tab = ({ text, tab, setTab, isActv }) => {
  const clickable = isActv || text === "Talent Info";
  return (
    <Box
      className={"py-1 flex-center" + (clickable ? " pointer" : "")}
      bgColor={text === tab ? "defaultBg" : "cardBg3"}
      width="50%"
      borderRadius={text === "Talent Info" ? "1rem 0 0 1rem" : "0 1rem 1rem 0"}
      onClick={() => {
        if (clickable) setTab(text);
      }}
    >
      <Text color={text === tab ? "black" : "defaultBg"} fontWeight={700}>
        {clickable ? text : ""}
      </Text>
    </Box>
  );
};
