import {
  Text,
  Hydro,
  Pyro,
  Cryo,
  Electro
} from "../../../styledCpns/DataDisplay";
import { Table } from "../StatsTable";
import { TalentLvMults } from "../../../data/characters/config";

const destruct = {
  NA: "Normal Attack",
  CA: "Charged Attack",
  PA: "Plunging Attack"
};

export function TalentInfo({ desc, isActv }) {
  if (!desc || !isActv) {
    return <Text className="pt-2">{desc || "To be added..."}</Text>;
  }
  return (
    <div className="flex-col">
      {desc.map(({ heading, content }, i) => (
        <div key={i} className="mt-2">
          {heading ? (
            <Text variant="h6" color="lightGold">
              {destruct[heading] ? destruct[heading] : heading}
            </Text>
          ) : null}
          <Text className={heading ? "" : "mt-1"}>
            {heading === "Elemental Absorption" ? (
              <>
                If {content} comes into contact with <Hydro>Hydro</Hydro>/
                <Pyro>Pyro</Pyro>/<Cryo>Cryo</Cryo>/<Electro>Electro</Electro>,
                it will deal additional <b>Elemental DMG</b> of that type.
                <br />
                Elemental Absorption may only occur once per use.
              </>
            ) : (
              content
            )}
          </Text>
        </div>
      ))}
    </div>
  );
}

const LeftCol = ({ children }) => (
  <Text className="pr-2" variant="subtitle1" color="#f0e873">
    {children}
  </Text>
);

export function SkillAttributes({ tlData, lv }) {
  if (!tlData) return null;
  const { stats, otherStats } = tlData;
  return (
    <div className="mt-2 pt-1">
      <Table>
        {stats
          .filter((stat) => !stat.conditional)
          .map((stat, i) => (
            <div key={i} className="pb-1 row">
              <LeftCol>{stat.name}</LeftCol>
              <Text variant="subtitle1" align="right">
                {stat.noCalc ? (
                  <b>{stat.getValue(lv)}</b>
                ) : (
                  <Value stat={stat} lv={lv} />
                )}
              </Text>
            </div>
          ))}
        {otherStats
          ? otherStats(lv).map((stat, j) => (
              <div key={"o" + j} className="pb-1 row">
                <LeftCol>{stat.name}</LeftCol>
                <Text variant="subtitle1" align="right">
                  {stat.value}
                </Text>
              </div>
            ))
          : null}
      </Table>
    </div>
  );
}

function Value({ stat, lv }) {
  const { baseSType, baseMult, multType, baseFlat } = stat;
  return (
    <b>
      {Array.isArray(baseMult)
        ? baseMult
            .map((mult) => getValue(mult, multType, lv, true, baseSType))
            .join("+")
        : baseMult || stat.lockedMult
        ? getValue(baseMult || stat.lockedMult, multType, lv, true, baseSType)
        : null}
      {baseMult && baseFlat ? " + " : ""}
      {baseFlat ? getValue(baseFlat, stat.flatType, lv, false) : null}
    </b>
  );
}

function getValue(base, type, lv, isPct, baseSType) {
  let result = base * TalentLvMults[type][lv];
  if (isPct) {
    result = Math.round(result * 100) / 100;
    result += "%";
  } else {
    result = Math.round(result);
  }
  if (baseSType) result += " " + baseSType;
  return result;
}
